<template>
  <Card class="card">
    <Breadcrumb>
      <BreadcrumbItem>运力管理</BreadcrumbItem>
      <BreadcrumbItem>运力匹配</BreadcrumbItem>
    </Breadcrumb>
    <div class="box-container">
      <Row class="title">
        <Col span="2">货种</Col>
        <Col span="20">
          <Cascader :data="getCargoList"
          class="cascader"
          placeholder="请选择货种"
          @on-change='selCargo'
          clearable>
         </Cascader>
        </Col>
      </Row>
      <Collapse v-model="value1" class="collapse">
        <Panel name="1">
          <span>航线</span>
          <Row slot="content">
            <Button type="primary">海运航线</Button>
          </Row>
          <Row slot="content" style="margin-top:20px">
            <Col span="1">装货港</Col>
            <Col span="20">
              <Button  @click="sonPort(item.children,index,3)" v-for="(item,index) in getSeaPort"
              :key="index"
              :type='seaZtype == index ? "primary" :"default" '
              size='small' style="margin:0px 5px 10px 5px">{{item.label}}</Button>
            </Col>
            </Row>
            <Row slot="content">
              <seaLoadSonPort
                @selSeaLoadPort='selSeaLoadPort'
                :childrenPort='seaLoadPortSon' :seaZtype='seaZtype'
              />
            </Row>
            <Row slot="content"><Divider/> </Row>
            <Row slot="content" style="margin:10px 0">
                <Col span="1">卸货港</Col>
                <Col span="23">
                  <Button @click="sonPort(item.children,index,4)" v-for="(item,index) in getSeaPort" :key="index"
                  :type='seaXtype == index ? "primary" :"default" '
                  size='small' style="margin:0px 5px 10px 5px"> {{item.label}}</Button>
                </Col>
            </Row>
            <Row slot="content">
                <seaDisSonPort
                @selSeaDisPort='selSeaDisPort'
                :childrenPort='seaDisPortSon' :seaXtype='seaXtype'
                />
            </Row>
            <Row slot="content" class="rows">
                <Col span="10" class="col">
                  <span class="marginLeft" style="margin-right:32px">装载日期</span>
                  <DatePicker
                    type="date"
                    placeholder="请选择装载日期"
                    style="width: 220px;display:inline-block"
                    @on-change='selLoadDate($event,1)'
                    format="yyyy/MM/dd"
                  >
                  </DatePicker>
                  <span class="marginLeft">±</span>
                  <Input class="input" placeholder="天数" v-model="seaDay">
                    <span slot="append">天</span>
                  </Input>
                </Col>
            </Row>
            <Row slot="content"><Divider/> </Row>

          <Row slot="content">
            <Button type="primary">江运航线</Button>
          </Row>
          <Row slot="content" style="margin-top:20px">
              <Col span="1">装货港</Col>
              <Col span="20">
                <Button  @click="sonPort(item.children,index,1)" v-for="(item,index) in getRiverPort"
                 :key="index"
                 :type='ztype == index ? "primary" :"default" '
                 size='small' style="margin:0px 5px 10px 5px">{{item.label}}</Button>
              </Col>
          </Row>
          <Row slot="content">
             <loadSonPort
              @selRiverLoadPort='selRiverLoadPort'
              :childrenPort='loadPortSon' :ztype='ztype'
             />
          </Row>
          <Row slot="content"><Divider/> </Row>
          <Row slot="content" style="margin:10px 0">
              <Col span="1">卸货港</Col>
              <Col span="23">
                <Button
                 @click="sonPort(item.children,index,2)"
                 v-for="(item,index) in getRiverPort"
                 :key="index"
                 :type='xtype == index ? "primary" :"default" '
                 size='small' style="margin:0px 5px 10px 5px"> {{item.label}}</Button>
              </Col>
          </Row>
           <Row slot="content">
              <disSonPort
               @selRiverDisPort='selRiverDisPort'
              :childrenPort='disPortSon' :xtype='xtype'
              />
           </Row>
           <Row slot="content"><Divider/> </Row>
           <Row slot="content">
              <Col span="24" class="col">
                <span class="marginLeft">预计装货日期</span>
                <DatePicker
                  type="date"
                  placeholder="预计装货起始日期"
                  style="width: 220px;display:inline-block"
                  format="yyyy/MM/dd"
                @on-change='selLoadDate($event,2)'></DatePicker>
                <span class="marginLeft">—</span>
                <DatePicker
                  type="date"
                  placeholder="预计装货结束日期"
                  style="width: 220px;display:inline-block"
                  format="yyyy/MM/dd"
                @on-change='selLoadDate($event,3)'></DatePicker>
            </Col>
           </Row>
        </Panel>
      </Collapse>
      <Collapse v-model="value2" class="collapse">
        <Panel name="1">
          <span>船型</span>
          <Row slot="content">
            <Button type="primary">海船</Button>
          </Row>
          <Row slot="content" class="row">
            <Col span="7" class="col">
              <span class="marginLeft">载货吨位</span>
              <Input style="width:100px"  placeholder="从" v-model="seaLoadTonsMin"> </Input>
              <span>—</span>
              <Input style="width:100px"  placeholder="至" v-model="seaLoadTonsMax"></Input>
            </Col>
            <Col span="7" class="col">
              <span class="marginLeft">吃水</span>
              <Input style="width:220px" class="common" placeholder="请输入吃水" v-model="seaFreat">
                <span slot="append">米</span>
              </Input>
            </Col>
            <Col span="7" class="col">
              <span class="marginLeft">TPC</span>
              <Input style="width:220px;margin-left:25px" class="common" placeholder="请输入TPC" v-model="seaTpc"></Input>
            </Col>
          </Row>

          <Row slot="content" class="row">
            <Col span="7" class="col">
              <span class="marginLeft">船长</span>
              <Input style="width:220px" class="common" placeholder="请输入船长" v-model="seaLong">
                <span slot="append">米</span>
              </Input>
            </Col>
            <Col span="7" class="col">
              <span class="marginLeft">船宽</span>
              <Input style="width:220px" class="common" placeholder="请输入船宽" v-model="seaWidth">
                <span slot="append">米</span>
              </Input>
            </Col>
          </Row>
          <Row slot="content">
            <Button type="primary">江船</Button>
          </Row>
          <Row slot="content" class="row">
            <Col span="7" class="col">
              <span class="marginLeft">载货吨位</span>
              <Input style="width:100px" placeholder="从" v-model="loadTonsMin"></Input>
              <span>—</span>
              <Input style="width:100px" placeholder="至" v-model="loadTonsMax"></Input>
            </Col>
            <Col span="7" class="col">
              <span class="marginLeft">船长</span>
              <Input style="width:220px" class="common" placeholder="请输入船长" v-model="length">
                <span slot="append">米</span>
              </Input>
            </Col>
          </Row>
        </Panel>
      </Collapse>
      <Row style="width:95%;height:70px">
        <Button size="large" class="pipei" @click="pipei" type="primary">开始匹配</Button>
      </Row>
      <Collapse v-model="value3" class="collapse">
        <Panel name="1">
          <pipeiResult
              slot="content"
              :seaResult1='seaResult1'
              :seaResult2='seaResult2'
              :riverResult1='riverResult1'
              :riverResult2='riverResult2'
              @pipeiRiver='pipeiRiver'
              @pipeiSea='pipeiSea'
           />
        </Panel>
      </Collapse>
    </div>
  </Card>
</template>
<script>
import pipeiResult from "./pipeiResult";
import loadSonPort from "./components/loadSonPort";
import disSonPort from "./components/disSonPort";
import seaDisSonPort from "./components/seaDisSonPort";
import seaLoadSonPort from "./components/seaLoadSonPort";
import { mapGetters, mapActions } from "vuex";
import portData from '@/config/port'
export default {
  data() {
    return {
      value1: "1",
      value2: "1",
      value3: "1",
      columns1: [],
      cargo:'',
      seaLoadPort:'',
      seaDisPort:'',
      riverLoadPort:'',
      riverDisPort:'',
      type:0,
      currentPage:1,
      size:10,
      loadDateStar:'',
      loadDateEnd:'',
      length:'',
      loadTonsMax:'',
      loadTonsMin:'',
      ztype:null,
      xtype:null,
      seaZtype:null,
      seaXtype:null,
      childrenPort:portData.riverPort,
      sonChildrenPort:portData.seaPort,
      loadPortSon:[],
      seaLoadPortSon:[],
      disPortSon:[],
      seaDisPortSon:[],
      seaLoadDate:'',
      seaDay:'',
      seaLoadTonsMax:'',
      seaLoadTonsMin:'',
      seaFreat:"",
      seaTpc:'',
      seaLong:'',
      seaWidth:'',
      riverResult1:{
        result:[],
        total:0
      },
      riverResult2:{
         result:[],
         total:0
      },
      seaResult1:{
         result:[],
         total:0
      },
      seaResult2:{
         result:[],
         total:0
      },
    };
  },
  computed: {
    ...mapGetters(["getCargoList", "getRiverPort", "getSeaPort",])
  },
  components: {
    pipeiResult,loadSonPort,disSonPort,seaDisSonPort,seaLoadSonPort
  },
  methods: {
    ...mapActions(["getCargoListFn", "getRiverPortFn", "getSeaPortFn"]),
    // 选择货种
    selCargo(e,selectedData){
      this.cargo =selectedData.length ? selectedData[1].label :''
    },
    // 江运装货港
    selRiverLoadPort(e){
      this.riverLoadPort = e
    },
    selRiverDisPort(e){
      this.riverDisPort = e
    },
    // 海运装货港
     selSeaLoadPort(e){
      this.seaLoadPort = e
    },
    selSeaDisPort(e){
      this.seaDisPort = e
    },
    selLoadDate(e,type=1){
      if(1===type){
        this.seaLoadDate = e
      }
      else if(2===type){
        this.loadDateStar = e
      }else{
        this.loadDateEnd = e
      }
    },
    checked(){
        if(!this.cargo){
          this.$Notice.error({
            desc:'请选择货种'
          })
          return false
        }
        if(this.loadDateStar || this.loadDateEnd){
          let check  = new Date(this.loadDateStar).getTime() > new Date(this.loadDateEnd).getTime()
          if(check){
              this.$Notice.error({
                desc:'预计装货日期起始日期不能大于结束日期'
              })
            return false
          }

        }
       if(this.seaLoadTonsMax || this.seaLoadTonsMin){
          if( JSON.parse(this.seaLoadTonsMax)  < JSON.parse(this.seaLoadTonsMin) ){
              this.$Notice.error({
                desc:'海船载货吨位区间有误,请重新输入.'
              })
             return false
            }
        }
        if( this.loadTonsMax || this.loadTonsMin){
          if(JSON.parse(this.loadTonsMax ) < JSON.parse( this.loadTonsMin)){
              this.$Notice.error({
                desc:'江船载货吨位区间有误,请重新输入.'
              })
              return
          }
        }
        return true
    },
    //NND  非要调四遍接口去匹配数据 哪有这样做的 SB
    // 把简单的事情 复杂化 辣鸡
    /**
     * @param type 0 上水航向 1 下水航向
     */
    async pipei(){
      if(!this.checked()) return
      Promise.all([ this.pipeiRiver(0),this.pipeiRiver(1),this.pipeiSea(0),this.pipeiSea(1)]).then(res=>{
            let result = res
            if(!result[0].total && !result[1].total && !result[2].total && !result[3].total){
              this.$Notice.info({
                  desc:'暂无匹配数据.'
                })
            }
            else{
                this.$Notice.info({
                  desc:'匹配成功.'
                })
            }
      })
    },
  //  江运匹配
    pipeiRiver(type = 0,currentPage,size){

      // 江运匹配数据
       let riverData = {
         cargo:this.cargo,
         type,
         loadPort:this.riverLoadPort ? this.riverLoadPort:this.loadPortSon[0].label,
         discPort:this.riverDisPort? this.riverDisPort:this.disPortSon[0].label,
         startTime:this.loadDateStar||0,
         endTime:this.loadDateEnd||0,
         maxTons:this.loadTonsMax || 0,
         minTons:this.loadTonsMin || 0,
         length:this.length || 0 ,
         currentPage:currentPage || this.currentPage,
         size:size||this.size,
       }
        return  new Promise(async(resolve, reject) => {
                      let res = await this.$http.riverShipListByInfo(riverData)
                      let result = res.data.result
                      let sendData = []
                      if(res.data.status == 0 && result instanceof Array){
                        result.map((item,i)=>{
                          sendData.push({
                              Index:Number((currentPage-1 || this.currentPage  -1) * (size || this.size) )+Number(i)+1,
                              shipName:item.shipnameCn || item.shipnameEn,
                              shipOwner: item.shipOwnerName,
                              loadTons:item.loadTons,
                              shipLong: item.length,
                              preGoods:item.discPrefer,
                              cooperation:item.count,
                              Hfreight:item.maxFreights,
                              Lfreight: item.minFreights,
                              id:item.id,
                              mmsi:item.mmsi
                          })

                        })

                      }
                      if(type==0){
                          this.riverResult1 = {result:[...sendData],total:res.data.total}
                          resolve(this.riverResult1)
                      }
                      else{
                           this.riverResult2 = {result:[...sendData],total:res.data.total}
                           resolve(this.riverResult2)
                      }

                  });
    },

  // 海运匹配
    pipeiSea(type = 0,currentPage,size){
     //  海运匹配数据
      let seaData = {
          cargo:this.cargo,
          type,
          loadPort:this.seaLoadPort ? this.seaLoadPort : this.seaLoadPortSon[0].label,
          discPort:this.seaDisPort ? this.seaDisPort:this.seaDisPortSon[0].label,
          loadDate:this.seaLoadDate||0,
          day:this.seaDay||0,
          maxTons:this.seaLoadTonsMax||0,
          minTons:this.seaLoadTonsMin||0,
          draught:this.seaFreat||0,
          tpc:this.seaTpc||0,
          length:this.seaLong||0,
          breadth:this.seaWidth||0,
          currentPage:currentPage||this.currentPage,
          size:size||this.size,
      }
        return   new Promise(async(resolve, reject) => {
                      let res = await this.$http.seaShipListByInfo(seaData)
                      let result = res.data.result
                      let sendData = []
                      if(res.data.status == 0 && result instanceof Array){
                        result.map((item,i)=>{
                          sendData.push({
                              Index:Number((currentPage-1 || this.currentPage  -1) * (size || this.size) )+Number(i)+1,
                              shipName:  item.shipnameCn || item.shipnameEn,
                              shipOwner: item.shipOwnerName,
                              loadTons:item.loadTons,
                              shipLong: item.length,
                              preGoods:item.discPrefer,
                              cooperation:item.count,
                              Hfreight:item.maxFreights,
                              Lfreight: item.minFreights,
                              id:item.id,
                              mmsi:item.mmsi
                          })

                        })

                      }
                     if(type==0){
                          this.seaResult1 = {result:[...sendData],total:res.data.total}
                          resolve(this.seaResult1)
                      }
                      else{
                           this.seaResult2 = {result:[...sendData],total:res.data.total}
                           resolve(this.seaResult2)
                      }
                  });
      },
    sonPort(value,i,type=1){
      if(1==type){
         this.ztype = i
         this.loadPortSon = [...value]
      }
      else if(2==type){
         this.xtype = i
        this.childrenPort = value
        this.disPortSon = [...value]
      }
      else if(3==type){
        this.seaZtype = i
        this.sonChildrenPort = value
        this.seaLoadPortSon = [...value]
      }
      else {
        this.seaXtype = i
        this.sonChildrenPort = value
        this.seaDisPortSon = [...value]
      }
    }
  },
  created(){
    this.loadPortSon = [...this.childrenPort];
    this.disPortSon = [...this.childrenPort];
    this.seaDisPortSon = [...this.sonChildrenPort];
    this.seaLoadPortSon = [...this.sonChildrenPort];
  },
  mounted() {
    this.getCargoListFn();
    this.getSeaPortFn();
    this.getRiverPortFn();
  }
};
</script>
<style lang="scss" scoped>
.title {
  border: 1px solid #f6f8f2;
  line-height: 80px;
  width: 95%;
  padding: 0px 20px;
  margin: 20px 0;
  border-radius: 3px;
  box-sizing: border-box;
  box-shadow: 0 0 2px #a8a8a8;
  position: relative;
}
.cascader{
  width:220px;
  position:absolute;
  top:25px;
  z-index:10000
}
.collapse {
  margin: 20px 0;
  width: 95%;
}
.ButtonGroup {
  margin-left: 30px;
}
.row {
  line-height: 60px;
}
.rows {
  line-height: 80px;
}
.col {
  position: relative;
}
.input {
  position: absolute;
  width: 80px;
  left: 343px;
  top: 25px;
}
.marginLeft {
  margin: 0 5px;
}
.common {
  position: absolute;
  left: 70px;
  top: 10px;
}
.pipei{
  background:#2b85e4;
  width:220px;
  color:#fff;
  float:right;
}
</style>



