<template>
  <div>
    <h3 >海船</h3>
    <p class="content">有船期船舶{{total3}}艘</p>
    <Table border :columns="columns1" :data="data3"></Table>
     <Page :total="total3"  @on-page-size-change='sizeChange($event,1)'
         class="pages" @on-change="changePage($event,1)" :current="currentPage1" :page-size="size1"
          show-elevator show-sizer  />
    <Divider/>
    <p class="content">无船期船舶{{total4}}艘</p>
    <Table border :columns="columns1" :data="data4"></Table>
     <Page :total="total4"  @on-page-size-change='sizeChange($event,2)'
         class="pages" @on-change="changePage($event,2)" :current="currentPage2" :page-size="size2"
          show-elevator show-sizer  />
    <Divider/>
    <h3 >江船</h3>
    <p class="content">下水航向{{total2}}艘</p>
    <Table border :columns="columns1" :data="data2"></Table>
     <Page :total="total2"  @on-page-size-change='sizeChange($event,3)'
         class="pages" @on-change="changePage($event,3)" :current="currentPage3" :page-size="size3"
          show-elevator show-sizer  />
    <Divider/>
    <p class="content">上水航向{{total1}}艘</p>
    <Table border :columns="columns1" :data="data1"></Table>
     <Page :total="total1"  @on-page-size-change='sizeChange($event,4)'
         class="pages" @on-change="changePage($event,4)" :current="currentPage4" :page-size="size4"
          show-elevator show-sizer  />
    <Divider/>
  </div>
</template>
<script>
export default {
    props:{
      riverResult1:{
        type:Object,
        required: true
      },
      riverResult2:{
        type:Object,
        required: true
      },
      seaResult1:{
        type:Object,
        required: true
      },
      seaResult2:{
        type:Object,
        required: true
      },
    },
    watch:{
      riverResult1(){
          this.data1 = this.riverResult1.result
          this.total1 = this.riverResult1.total
      },
      riverResult2(){
          this.data2 = this.riverResult2.result
          this.total2 = this.riverResult2.total
      },
      seaResult1(){
          this.data3 = this.seaResult1.result
          this.total3 = this.seaResult1.total
      },
      seaResult2(){
          this.data4 = this.seaResult2.result
          this.total4 = this.seaResult2.total
      },
    },
    data(){
        return {
             riverShip1:{},
             riverShip2:{},
             seaShip1:{},
             seaShip2:{},
             columns1: [
                    {
                        title: '#',
                        key: 'Index',
                        width: 60,
                        align:'center',
                    },
                    {
                        title: '船名',
                        key: 'shipName',
                        align:'center',
                    },
                    {
                        title: '船东',
                        key: 'shipOwner',
                         align:'center',
                    },
                    {
                        title: '装载吨位',
                        key: 'loadTons',
                         align:'center',
                    },
                    {
                        title: '船长',
                        key: 'shipLong',
                         align:'center',
                    },
                    {
                        title: '卸货偏好',
                        key: 'preGoods',
                         align:'center',
                    },
                    {
                        title: '合作次数',
                        key: 'cooperation',
                         align:'center',
                    },
                    {
                        title: '最高运价',
                        key: 'Hfreight',
                         align:'center',
                    },
                    {
                        title: '最低运价',
                        key: 'Lfreight',
                         align:'center',
                    },
                    {
                        title: '详情',
                        key: 'detail',
                         align:'center',
                         render: (h, params) => {
                           return h('Button', {
                             props:{
                               type:"primary"
                             },
                              style:{
                                display:'inline-block',
                                width:100,
                                background:'red;'
                              },
                              on:{
                                click:()=>{
                                    // console.log(123,params)
                                    let routeUrl = this.$router.resolve({
                                      path: "/yunli/yunliship",
                                      query: {mmsi:params.row.mmsi}
                                });

                                  window.open(routeUrl .href, '_blank');

                                }
                              }
                           },'查看')
                       }
                    },
                ],
                data1: [],
                data2:[],
                data3:[],
                data4:[],
                currentPage1:1,
                currentPage2:1,
                currentPage3:1,
                currentPage4:1,
                size1:10,
                size2:10,
                size3:10,
                size4:10,
                total1:0,
                total2:0,
                total3:0,
                total4:0,
        }
    },
    methods:{
     changePage(currentPage,type){
           switch(type){
             case 1:
               this.currentPage1 = currentPage;
               this.$emit('pipeiSea',0,currentPage,this.size1)
               break
             case 2:
                this.currentPage2 = currentPage;
                this.$emit('pipeiSea',1,currentPage,this.size2)
               break
             case 3:
               this.currentPage3 = currentPage;
               this.$emit('pipeiRiver',1,currentPage,this.size3)
               break
             case 4:
                this.currentPage4 = currentPage;
                this.$emit('pipeiRiver',0,currentPage,this.size4)
               break
           }

          },
      sizeChange(size,type){
           switch(type){
             case 1:
               this.size1 = size;
               this.currentPage1 =1
               this.$emit('pipeiSea',0,this.currentPage1,size)
               break
             case 2:
                this.size2 = size;
                this.currentPage2 =1
                this.$emit('pipeiSea',1,this.currentPage2,size)
               break
             case 3:
                this.size3 = size;
                 this.currentPage3 =1
               this.$emit('pipeiRiver',1,this.currentPage3,size)
               break
             case 4:
                this.size4 = size;
                 this.currentPage4 =1
                this.$emit('pipeiRiver',0,this.currentPage4,size)
               break
           }
        },
    }
}
</script>
<style lang="scss" scoped>
 $color:20px;
.content{
    line-height: 55px;
}
/deep/  .ivu-page{
  position: relative;
}
</style>



