<template>
  <div>
    <Button size='small'
      v-for="(item,index) in childrenPort"
      :key="index"  style="margin:0px 5px 10px 5px"
       :type='sonType === index ? "primary" :"default"'
       @click="getPort(item.label,index)"
      >{{item.label}}
    </Button>
  </div>
</template>

<script>
export default {
  props:{
    childrenPort:{
      type:Array,
      required: true
    },
    ztype:{
    }
  },
  watch:{
    ztype(){
      this.sonType = 0
    }
  },
  data(){
    return {
      sonType:null,
    }
  },
  methods:{
    getPort(v,i){
      // console.log(v,i)
      this.sonType = i
      // this.selRiverLoadPort(i)
      this.$emit('selRiverLoadPort',v)
    }
  },
  mounted(){

  }
}
</script>
<style lang="scss" scoped>

</style>



